
<template>
  <v-container>
     
    <v-row class="mb-6" v-if="!isWakel">
      <v-col :cols="$vuetify.breakpoint.name == 'xs' ? 12 : 6" >
        <total-panel title="Today" :count="totals.today.count" :amount="totals.today.amount"></total-panel>
      </v-col>
      <v-col :cols="$vuetify.breakpoint.name == 'xs' ? 12 : 6">
        <total-panel title="This month" :count="totals.month.count" :amount="totals.month.amount"></total-panel>
      </v-col>
    </v-row>
    <!-- admin section -->
    <v-row v-if="isAdmin">
      <total-sheet v-for="business in businesses" :key="business._id" :title="business.name" :id="business._id"></total-sheet>
    </v-row>
     <!-- manager section -->
     <v-row v-if="isAgent">
        <!-- FTDs -->

      
      <v-toolbar flat>
        <v-toolbar-title>
          FTDs
        </v-toolbar-title>
        <v-spacer></v-spacer>
        
     
      </v-toolbar>
   
       <v-data-table :headers="headers" :items="ftds"  @click:row="$router.push('/deposit/new?clone='+$event._id+'&fields=customer&fields=payment_method')"> 
     <template v-slot:item.created_on="{ item }" >
        {{ item.created_on.split('T')[0] }} 
      </template> 
      <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
      </template>
    </v-data-table>
    </v-row>
    <v-divider class="my-6" v-if="isAgent"></v-divider>

    <v-row v-if="isAgent">



      <v-toolbar flat>
        <v-toolbar-title>
          Today deposits
        </v-toolbar-title>
        <v-spacer></v-spacer>
        
        <v-btn  color="green white--text mx-2"  @click="$router.push('/deposit/new')"> <v-icon>mdi-plus</v-icon> New deposit</v-btn>
      
      
      </v-toolbar>
       <v-data-table :headers="headers" :items="deposits"  @click:row="$router.push('/deposit/'+$event._id)"> 
     <template v-slot:item.created_on="{ item }" >
        {{ item.created_on.split('T')[0] }} 
      </template> 
      <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
      </template>
    </v-data-table>
    </v-row>
    <v-divider class="my-6" v-if="isAgent"></v-divider>
    <v-row v-if="isAgent">

       <v-data-table :headers="mheaders" :items="mdeposits"  @click:row="goTo" style="width:100%"> 
          <template v-slot:item._id.date="{ item }" >
              {{ item._id.date.split('T')[0] }} 
            </template> 
            <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
            </template>
        </v-data-table>
    </v-row>


  <!-- AFFILIATE SECTION -->
    <affiliate-deposits v-if="isAffiliate"></affiliate-deposits>
    <v-divider class="my-6" v-if="isAffiliate"></v-divider>

    <affiliate-upsales v-if="isAffiliate" ></affiliate-upsales>

  <v-row   v-for="wakel in wakels" :key="wakel._id">
    <wakel-transactions :wakel="wakel"></wakel-transactions>
  </v-row>
   
  </v-container>
</template>
<script>

 

import TotalPanel from './../components/dashboard/Panel'
import TotalSheet from './../components/dashboard/TotalSheet'

import AffiliateDeposits from './../components/dashboard/AffiliateDeposits'
import AffiliateUpsales from './../components/dashboard/AffiliateUpsales'

import WakelTransactions from './../components/dashboard/WakelTransactions'


import collections from './../collections'


const headers = collections.get('deposit').table.filter(e => e.value !== 'actions')
import { mapActions, mapState } from 'vuex'
import $ from 'jquery' 
import dateformat from 'dateformat'






export default {
  name: 'Dashboard',
  components: {
    TotalPanel,
    TotalSheet,
    AffiliateDeposits,
    AffiliateUpsales,
    WakelTransactions
     
  },
  data () {

   
    return {
       
      totals: {
        today: {
          count: 0,
          amount: 0
        },
        month: {
          count: 0,
          amount: 0
        }
      },
      businesses: [],
      deposits: [],
      mdeposits: [],
      ftds: [],
      headers,
      mheaders:[
         {
          text: 'Date',
          align: 'start',
          value: '_id.date',
          width: '30%'
        },
         {
          text: 'Created By',
          align: 'start',
          value: '_id.user.displayName',
          width: '30%'
        },
        { text: 'Deposits', value: 'count', width: '30%' },
        { text: 'Total', value: 'amount' }
      ],
      wakels: [] 
       

    }
  },
  computed: {
    ...mapState({ 
      user: state => state.auth.user
    }),
    isAdmin () {
      
        return !this.user.business && !this.isAffiliate && !this.isWakel
    },
    isAgent () {
     
      return !this.isAdmin && !this.isAffiliate && !this.isWakel
    },
    isAffiliate () {
      return this.user.affiliate  
    },
    isWakel () {
      const transaction = this.user.rights.filter(e => e.collection == 'transaction')
      return transaction[0] ? transaction[0].read.allOwn == 'own' : false
    },
    today () {
      let today = new Date();
      today.setHours(0, 0, 0, 0);
      return today

    },
    first_day_of_the_month() {
      const today = new Date()
      return new Date(today.getFullYear(), today.getMonth(), 1)

    }
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    goTo (item) {
      
      const query = {
       'user': item._id.user._id, 
      //  'business': this.id, 
       'created_on': dateformat(item._id.date,'yyyy-mm-dd'),
        sortBy: ['created_on'],
        sortDesc: [false]
      }
      // console.log(query, $, item);
      this.$router.push('/deposit?' + $.param(query))
    },
    async getTotals () {
      const totalToday = await this.get({ collection: 'dashboard/total', query:{
        created_on: '>='+this.today
      } })
       
      const totalMonth= await this.get({ collection: 'dashboard/total', query:{
        created_on: '>='+this.first_day_of_the_month
      } })

      if (totalToday && totalToday.status == 200 && totalToday.data.length) {
        this.totals.today.count = totalToday.data[0].count
        this.totals.today.amount = totalToday.data[0].amount
      }


      if (totalMonth && totalMonth.status == 200 && totalMonth.data.length) {
        this.totals.month.count = totalMonth.data[0].count
        this.totals.month.amount = totalMonth.data[0].amount
      }
    
    },
   async  getDesksTotals () {
      if (this.isAdmin) {
        const businesses = await this.get({ collection: 'business', query: {
          sortBy: ['name'], sortDesc: [true]
        } })
        if (businesses && businesses.status == 200 && businesses.data.docs.length)
          this.businesses = businesses.data.docs
         
      }
    },
    async getTodayDeposits () {
      const deposits = await this.get({ collection: 'deposit', query:{
         'created_on': '>='+this.today
      } })
      this.deposits = deposits.data.docs
    },
     async getMonthDeposits () {
      const deposits = await this.get({ collection: 'dashboard/grouped', query:{
         'created_on': '>='+ this.first_day_of_the_month
      } })
      this.mdeposits = deposits.data
    },
    async getFTDs() {
      const ftds = await this.get({ collection: 'dashboard/ftds', query:{
         'created_on': '>='+ this.first_day_of_the_month,
         'assign': this.user._id
      } })
      this.ftds = ftds.data
    },
    async getWakels () {
      if (this.isAdmin) {
        const users = await this.get({ collection: 'user', query: { role: 'wakel' } })
        this.wakels = users.data.docs
      }
    }
  },
  async created () {
    this.getTotals()
    this.getDesksTotals()
    this.getTodayDeposits()
    this.getMonthDeposits()
    this.getFTDs()
    this.getWakels()
    // affiliate
    // const upsales = await this.get({ collection: 'report/affiliate', query: { 
    //   created_on: this.dates.join('|'),
    //   id: this.user.affiliate
    // }})




  


  }
}
</script>