<script>
function pad(number, length) {
   
    var str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}
 

// import $ from 'jquery' 
import dateformat from 'dateformat'
import { mapActions } from 'vuex'
import collections from './../../collections'
 
export default {
  name: 'WakelTransactions',
  props: {
    wakel: Object
  },
  data() {
     let today = new Date();
    today.setHours(0, 0, 0, 0);
    const dates = [`${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-${pad(today.getDate(),2)}`]
    const collection = collections.get('transaction')
    return {
      dateformat,
      dates,
      wakels: [],
      loading: false,
      headers: collection.table.filter(e => e.value !== 'actions'),
      items: [],
      total: {}
     
    }
   
  },
  watch:{
    dates () {
      this.getTrans()
    },
    
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    async getTotals () {
      this.loading = true
    const query = { 
      created_on: this.dates.join('|'),
      user: this.wakel._id
    }
       

        const total = await this.get({ collection: 'transaction/total', query })
        if (total && total.status == 200) {
          this.total = total.data[0]
        console.log( this.total);
        }
      
       
        
        this.loading = false

    }
     
  },
  computed: {
    today () {
      let today = new Date();
      today.setHours(0, 0, 0, 0);
      return today
    },
    first_day_of_the_month() {
      return new Date(this.today.getFullYear(), this.today.getMonth(), 1)
    }
  },
  async created () {
    this.getTotals()
  
   

  } 
}
</script>
