<template>
  <v-sheet :color="color" class=" mb-3 mx-3 " width="100%" elevation="2" >
    <v-toolbar class="mb-3" dense flat :color="color">
      <v-toolbar-title class="title ">{{title}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <v-btn outlined class="mx-2" @click="$emit('table', 'sale')">Sales</v-btn>
      <v-btn outlined class="mx-2" @click="$emit('table', 'retention')">Retentions</v-btn> -->
    </v-toolbar>
  

    <v-row class="mx-1">
      <v-col class="mx-2 pa-0 px-2" v-for="(item, ind) in departments" :key="ind" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null">
         
        <fieldset class="px-2 mb-2 fielset">
          <legend>{{ item._id.name }}</legend>
          <v-row>
            <v-col ><small>Deposits</small><h2>{{ item.count || 0 }}</h2></v-col>
            <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount || 0)}}</h2></v-col>
          </v-row>
          </fieldset>
      </v-col>
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" >
         
        <fieldset class="px-2 mb-2 fielset">
          <legend>Total today</legend>
          <v-row>
            <v-col  ><small>Deposits</small><h2>{{ todayTotal.count || 0 }}</h2></v-col>
            <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(todayTotal.amount || 0)}}</h2></v-col>
          </v-row>
          </fieldset>
      </v-col>
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null">
        
      <fieldset class="px-2 mb-2 fielset">
        <legend>Total this month</legend>
        <v-row>
          <v-col ><small>Deposits</small><h2>{{ monthTotal.count || 0 }}</h2></v-col>
          <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(monthTotal.amount || 0)}}</h2></v-col>
        </v-row>
        </fieldset>
      </v-col> 
    </v-row>
    <v-row>
      <v-col>
        <v-btn outlined v-for="(item, ind) in departmentsNames" :key="ind" class="mx-2" @click="table(item._id)">{{item.name}}</v-btn>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog" max-width="950px"  >
       <v-card>
        <v-card-title>
           {{dialogTitle}}
           <v-spacer></v-spacer>
      
           <v-btn icon @click="dialog = false"><v-icon>mdi-close</v-icon></v-btn>
        </v-card-title>
        <v-card-text>
          
                <v-data-table  v-if="dialog"
                  :headers="headers"
                  :items="items"
                  multi-sort
                  class="elevation-1"
                  @click:row="goTo"
                >
                
                <template v-slot:item.amount="{ item }" >
                  {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}
                </template>
                </v-data-table> 
 
         </v-card-text>
       </v-card>
     </v-dialog>
  </v-sheet>
</template>
<script>
import $ from 'jquery' 
import dateformat from 'dateformat'
import { mapActions } from 'vuex'
export default {
  name: 'TotalSheet',
  props: {
    title: String,
    id: String,
    // items: Array,  
    // items2: Array,  
    color: {
      type: String,
      default: 'blue lighten-4'
    }
  },
  data: () => ({
    dialog: false,
    dialogTitle: '',
    items: [],
    headers: [
        {
            text: 'Created By',
            align: 'start',
            value: '_id.displayName',
        },
        { text: 'Deals', value: 'count' },
        { text: 'Amount', value: 'amount' },
    ],
    selected: 'month',
    departments: [],
    departmentsNames: [],
    todayTotal: {
      count: 0,
      amount: 0
    },
    monthTotal: {
      count: 0,
      amount: 0
    }
  }),
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    async table (department) {
      console.log(this.id, department);
      const users = await this.get({ collection: 'dashboard/tables', query:{
        'created_on': '>=' + dateformat(this.first_day_of_the_month,'yyyy-mm-dd'), 
        business: this.id,   
        department: department 
      } })
      this.items = users.data
      console.log(users);
      this.dialog = true
    },
    goTo (item) {

      const query = {
        'user': item._id._id, 
        'business': this.id, 
        'created_on': '>=' + dateformat(this.first_day_of_the_month,'yyyy-mm-dd'), 
        sortBy: ['created_on'],
        sortDesc: [false]
      }
      this.$router.push('/deposit?' + $.param(query))
    }
  },
  computed: {
    today () {
      let today = new Date();
      today.setHours(0, 0, 0, 0);
      return today
    },
    first_day_of_the_month() {
      return new Date(this.today.getFullYear(), this.today.getMonth(), 1)
    }
  },
  async created () {

    const departments = await this.get({ collection: 'department' })
    if (departments && departments.status == 200){
      this.departmentsNames = departments.data.docs
    }

    const today = await this.get({ collection: 'dashboard/business', query:{
        'created_on':  '>=' + dateformat(this.today,'yyyy-mm-dd'), 
        business: this.id 
      } })
 console.log(today );
      if (today && today.status == 200){
        this.departments = today.data.departmentsTotal
        if (today.data.total[0]) {
          this.todayTotal.count = today.data.total[0].count
          this.todayTotal.amount = today.data.total[0].amount

        }
      }

      const month = await this.get({ collection: 'dashboard/total', query:{
         'created_on':  '>=' + dateformat(this.first_day_of_the_month,'yyyy-mm-dd'), 
          business: this.id 
        }
        })
       if (month && month.status == 200){
        if (month.data[0]) {
          this.monthTotal.count = month.data[0].count
          this.monthTotal.amount = month.data[0].amount

        }
      }
  
  } 
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>