<template>
<v-data-table 
  :loading="loading"
    :single-expand="true"
    :expanded.sync="expanded"
    show-expand
    item-key="_id"
    :headers="headers"
    :items="items"
    >
    <template v-slot:top>
      <v-toolbar flat>
        <v-icon size="25" class="mx-2">mdi-chart-line</v-icon>

        <v-toolbar-title>Upsales</v-toolbar-title>
        <v-spacer></v-spacer>
        <app-date-range label="Date" v-model="dates" hide-details></app-date-range>
      </v-toolbar>
    </template>
    <template v-slot:loading   >
        <v-skeleton-loader
        class="mt-6"
        height="94"
        type="list-item-three-line"
      >
      </v-skeleton-loader>
    </template>
     <template v-slot:item.amount="{ item }" >
          {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
    </template>   
    <template v-slot:item.total.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.total.amount) }}  
    </template>   
    <template v-slot:item.created_on="{ item }"  >
      {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
    </template>
    <template v-slot:item.count="{ item }"  >
      {{ item.child.length }}
    </template>


     <template v-slot:expanded-item="{ headers, item }">
      <td :colspan="headers.length" class="pa-4">
        <v-data-table :headers="dataHeaders" :items="item.child" @click:row="innerListClick($event, item)">
          <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
          </template>   
           <template v-slot:item.total.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.total.amount) }}  
          </template> 
          <template v-slot:item.created_on="{ item }"  >
            {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
          </template>
           <template v-slot:item.count="{ item }"  >
            {{ item.child.length }}
          </template>
           
        </v-data-table>
      </td>
    </template> 
</v-data-table>



</template>

<script>
function pad(number, length) {
   
    var str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}
import appDateRange from './../DateRange'

// import $ from 'jquery' 
import dateformat from 'dateformat'
import { mapActions } from 'vuex'

 
export default {
  name: 'AffiliateuUpsales',
  components:{  
    appDateRange
  },
  data() {
     let today = new Date();
    today.setHours(0, 0, 0, 0);
    const dates = [`${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-${pad(today.getDate(),2)}`]
   
    return {
      dateformat,
      dates,
      loading: false,
      expanded:[],
      headers: [ {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
            width: '10%'
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        // { text: 'Affiliate', value: 'affiliate.name' },

        { text: 'FTD', value: 'amount' },
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'total.amount' },
        { text: 'Date', value: 'created_on' }],
      items: [],
      dataHeaders: [ { text: 'Date', value: 'created_on' },
         { text: 'Amount', value: 'amount' },]
    }
  },
  watch: {
    dates () {
      this.getUpsales()
    }
  },
  methods:{
    ...mapActions({
      get: 'api/get',
    }),
    async getUpsales () {
     this.loading = true
    const upsales = await this.get({ collection: 'report/affiliate', query: { 
      created_on: this.dates.join('|'),
      id: this.$store.state.auth.user.affiliate
    }})
 
    this.items = upsales.data[0].child
     this.loading = false

    }
  },
  created () {
    this.getUpsales()
  }
}
</script>
