<template>
  <v-data-table 
  :loading="loading"
    :single-expand="true"
    :expanded.sync="expanded"
    show-expand
    item-key="_id._id"
    :headers="headers"
    :items="items"
    >
    <template v-slot:top>
      <v-toolbar flat>
        <v-icon size="33"  class="mx-2">mdi-bank-transfer-in</v-icon>
        <v-toolbar-title>Deposits</v-toolbar-title>
        <v-spacer></v-spacer>
        <app-date-range label="Date" v-model="dates" hide-details></app-date-range>
      </v-toolbar>
    </template>
    <template v-slot:loading   >
        <v-skeleton-loader
        class="mt-6"
        height="94"
        type="list-item-three-line"
      >
      </v-skeleton-loader>
    </template>
    <template v-slot:item.amount="{ item }" >
      {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
    </template>

     <template v-slot:expanded-item="{ headers,  }">
      <td :colspan="headers.length" class="pa-4">
        <v-data-table :headers="dataHeaders" :items="dataItems" :loading="exLoading" >
          <template v-slot:loading   >
              <v-skeleton-loader
              class="mt-6"
              height="94"
              type="list-item-three-line"
            >
            </v-skeleton-loader>
          </template>
          <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
          </template>   
      
          <template v-slot:item.created_on="{ item }"  >
            {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
          </template>
           
        </v-data-table>
      </td>
    </template> 
  </v-data-table>
</template>
<script>
function pad(number, length) {
   
    var str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}
import appDateRange from './../DateRange'

// import $ from 'jquery' 
import dateformat from 'dateformat'
import { mapActions } from 'vuex'

const affiliateDepositsHeaders = [
  {
    text: 'TPC',
    align: 'start',
    value: '_id.tpc',
  },
  {
    text: 'Account',
    value: '_id.name',
  },
  { text: 'Deposits', value: 'count', width: '30%' },
  { text: 'Total', value: 'amount' },

]

const leadHeaders = [
         { text: 'Date', value: 'created_on' },
         { text: 'Amount', value: 'amount' },
        
        ]
export default {
  name: 'AffiliateDeposits',
  components: {
    appDateRange
  },
  data() {
     let today = new Date();
    today.setHours(0, 0, 0, 0);
    const dates = [`${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-${pad(today.getDate(),2)}`]
   
    return {
      dateformat,
      dates,
      expanded: [],
      dialog: false,
      dialogTitle: '',
      loading: false,
      headers: affiliateDepositsHeaders,
      items: [],
      exLoading: false,
      dataHeaders: leadHeaders,
      dataItems: []
    }
   
  },
  watch:{
    dates () {
      this.getDeposits()
    },
     async expanded (item) {
       console.log(item[0]);
      if(item.length) {
        this.exLoading = true
         const customerId = item[0]._id._id
        const result = await this.get({ collection: 'deposit', query: {
            created_on: this.dates.join('|'),
            customer: customerId,
            limit: 1000,

            sortBy:['created_on'],
            sortDesc: [true]
        }})
        console.log(result);
          this.dataItems = result.data.docs
        this.exLoading = false

      }
    },
    
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    async getDeposits () {
      this.loading = true
    const query = { 
                created_on: this.dates.join('|'),
              'affiliate._id' :  this.$store.state.auth.user.affiliate
              }
        const affiliate_deposits = await this.get({ 
          collection: 'report_old/affiliate', 
          query: { 
            groupBy: 'customer._id', ...query, 
            meta: {collection: 'customer'}, sortBy: ['customer.tpc', 'created_on'], sortDesc:[1, 1] } })
      
        this.items = affiliate_deposits.data
        
        this.loading = false

    }
     
  },
  computed: {
    today () {
      let today = new Date();
      today.setHours(0, 0, 0, 0);
      return today
    },
    first_day_of_the_month() {
      return new Date(this.today.getFullYear(), this.today.getMonth(), 1)
    }
  },
  async created () {
   this.getDeposits()
  
  } 
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>